#spreadsheet-container{
    height: 88vh;
}
.x-spreadsheet{
    height: 88vh;
}
.x-spreadsheet > .x-spreadsheet-toolbar{
    width: 100% !important;
}

.x-spreadsheet-dropdown .x-spreadsheet-dropdown-content{
    overflow-y: scroll;
    height: 500px;
}

@media screen and (max-width: 480px){
    .x-spreadsheet-toolbar{
        display: flex;
    }
    
}


