.title-container{
    padding:5px 10px ;
}

.title{
    height: 20px;
    width: fit-content;
    border: none;
    /* padding: 12px 5px; */
    letter-spacing: 2px;
    font-family: Roboto, RobotoDraft, Helvetica, Arial, sans-serif;
    font-size: 14px;
}
.title:focus{
    outline:none;
}


@media screen and (max-width: 480px){
    .title{
        margin: 0;
    }
}